import { Link } from "react-router-dom";
import { useEffect } from "react";
import "./CSS/sideNav.css";

const MobileNav = () => {
   function fnOpenSidenavOnSwipe() {
      //.Touch coordinates
      let touchstartX,
         touchendX,
         touchstartY,
         touchendY = 0;
      //.touch start
      document.addEventListener(
         "touchstart",
         (e) => {
            touchstartX = e.changedTouches[0].screenX;
            touchstartY = e.changedTouches[0].screenY;
            //.console.log("start " + touchstartX);
         },
         false
      );
      //.touch end
      document.addEventListener(
         "touchend",
         (e) => {
            touchendX = e.changedTouches[0].screenX;
            touchendY = e.changedTouches[0].screenY;
            //.console.log("end " + touchendX);
            fnRightSwipe();
         },
         false
      );
      //.right swipe
      const fnRightSwipe = () => {
         //.getting the translateX property
         let sideNav = document.querySelector(".sidenav");
         let style = window.getComputedStyle(sideNav);
         let translateX = new window.WebKitCSSMatrix(style.transform);
         //.console.log(translateX.m41);
         if (
            //#if the translateX is (not 0) then this code will run
            translateX.m41 !== 0 &&
            touchendX > touchstartX + 50 &&
            touchendY > touchstartY - 50 && //.50px above
            touchendY < touchstartY + 50 //.50px below
         ) {
            //.console.log("swiped right");
            let elem = document.querySelector(".sidenav");
            let instance = window.M.Sidenav.init(elem, {});
            instance.open();
         }
      };
   }

   useEffect(() => {
      document.querySelector(".sideNavBtn").addEventListener("click", () => {
         var elem = document.querySelector(".sidenav");
         var instance = window.M.Sidenav.init(elem, {});
         instance.close();
      });

      document.getElementById("home").addEventListener("click", () => {
         var elem = document.querySelector(".sidenav");
         var instance = window.M.Sidenav.init(elem, {});
         instance.close();
      });

      document.addEventListener("DOMContentLoaded", () => {
         fnOpenSidenavOnSwipe();
      });
   }, []);

   return (
      <ul className="sidenav" id="mobile-demo">
         <li>
            <Link to="/dojoBlog" id="home">
               <span
                  style={{
                     fontSize: "20px",
                     color: "#f1356d",
                     marginLeft: "20px",
                     fontWeight: "900",
                  }}
               >
                  Home
               </span>
            </Link>
         </li>
         <li>
            <Link
               to="/dojoBlog/create"
               className="waves-effect waves-light btn white-text sideNavBtn"
            >
               New Blog
            </Link>
         </li>
      </ul>
   );
};

export default MobileNav;
